.container {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
}

@media (min-width: 1200px) {
    .container {
      max-width: 1140px;
    }
}

@media (max-width: 992px){
    .container {
      max-width: 960px;
    }
}
  
@media (max-width: 768px){
    .container {
      max-width: 720px;
    }
}
  
@media (max-width: 576px){
    .container {
        max-width: 540px;
    }
}